'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import toast from 'react-hot-toast'
import FormInput from '@/components/FormInput'

export default function RegisterPage() {
  const router = useRouter()

  const [formData, setFormData] = useState({
    first_name: '',
    surname: '',
    other_names: '',
    pen_number: '',
    email: '',
    password: '',
    confirm_password: '',
  })

  const [errors, setErrors] = useState<Record<string, string>>({})

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData({ ...formData, [e.target.name]: e.target.value })
    setErrors((prev) => ({ ...prev, [e.target.name]: '' }))
  }

  const validatePassword = (password: string) => {
    const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@#$%^&+=!]).{8,}$/
    return regex.test(password)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    const newErrors: Record<string, string> = {}

    if (!formData.first_name) newErrors.first_name = 'First name is required'
    if (!formData.surname) newErrors.surname = 'Surname is required'
    if (!formData.pen_number) newErrors.pen_number = 'PEN number is required'
    if (!formData.email) newErrors.email = 'Email is required'
    if (!formData.password) {
      newErrors.password = 'Password is required'
    } else if (!validatePassword(formData.password)) {
      newErrors.password = 'Password must be at least 8 characters and include uppercase, lowercase, number, and special character'
    }

    if (formData.confirm_password !== formData.password) {
      newErrors.confirm_password = 'Passwords do not match'
    }

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors)
      return
    }

    try {
      const res = await fetch('http://localhost:5000/api/auth/register', {
        method: 'POST',
        credentials: 'include',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(formData),
      })

      const data = await res.json()
      if (!res.ok) {
        toast.error(data.error || 'Something went wrong')
        return
      }

      toast.success(data.message || 'Registration successful!')
      router.push('/login')
    } catch {
      toast.error('Registration failed. Try again later.')
    }
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-100">
      <div className="w-full max-w-md bg-white p-6 rounded shadow">
        <h2 className="text-2xl font-semibold text-center mb-6">Register</h2>
        <form onSubmit={handleSubmit} className="space-y-4">
          <FormInput label="First Name" name="first_name" value={formData.first_name} onChange={handleChange} error={errors.first_name} />
          <FormInput label="Surname" name="surname" value={formData.surname} onChange={handleChange} error={errors.surname} />
          <FormInput label="Other Names (Optional)" name="other_names" value={formData.other_names} onChange={handleChange} />
          <FormInput label="PEN Number" name="pen_number" value={formData.pen_number} onChange={handleChange} error={errors.pen_number} />
          <FormInput label="Email" name="email" type="email" value={formData.email} onChange={handleChange} error={errors.email} />
          <FormInput label="Password" name="password" type="password" value={formData.password} onChange={handleChange} error={errors.password} />
          <FormInput label="Confirm Password" name="confirm_password" type="password" value={formData.confirm_password} onChange={handleChange} error={errors.confirm_password} />
          <button type="submit" className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition">Register</button>
        </form>
        <p className="text-sm text-center mt-4 text-gray-600">
          Already have an account? <a href="/login" className="text-blue-600 hover:underline">Login</a>
        </p>
      </div>
    </div>
  )
}
